<template>
	<div id="kanjiagoods-post">
		<public-header title="添加砍价商品"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell class="no-fix flex ">
					<div class="padding-10-b flex-lr">
						<div>商品图片</div>
						<div class="c-gray">此图片为商品列表页图片</div>
					</div>
					<div class="goods-thumb-wrap">
						<img class="goods-thumb " :src="records.thumb" alt="" v-if="records.thumb.length > 0">
						<img class="goods-thumb " src="static/img/add_pic.png" alt="" v-else>
						<input class="weui-uploader__input" type="file" multiple="multiple" accept="image/*" @change="onUploadImage($event)">
					</div>
				</van-cell>
				<van-cell>
				<div class="upload-img">
					<div class="flex-lr goods-img">
						<div>商品图集</div>
						<div class="c-gray">最多显示4张，此图片为商品图集</div>
					</div>
					<uploader @onGetUrl="onChangeThumbs" @delete="onChangeThumbs" :max="4" :value="thumbs"></uploader>
				</div>
				</van-cell>
				<van-field
					v-model="records.code"
					label="商品编码"
					placeholder="请填写商品编码"
					input-align="right"
				/>
				<van-field
					v-model="records.name"
					label="商品名称"
					placeholder="请填写商品名称"
					input-align="right"
				/>
				<van-field
					v-model="records.total"
					label="商品库存"
					placeholder="请填写商品库存"
					input-align="right"
				/>
				<van-field
					v-model="records.unit"
					label="商品单位"
					placeholder="请填写商品单位"
					input-align="right"
				/>
				<van-field
					v-model="records.detail"
					label="商品详情"
					type="textarea"
					placeholder="请填写商品详情"
					rows="1"
					input-align="right"
					autosize
				/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="商品分类">
					<div slot="right-icon" class="flex" @click="onTogglePopup">
						<span>{{records.category_title ? records.category_title : '请选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-field
					v-model="records.oldprice"
					label="商品原价"
					type="number"
					placeholder="请输入商品原价"
					input-align="right"
				/>
				<van-field
					v-model="records.price"
					label="活动底价"
					type="number"
					placeholder="请输入活动底价"
					input-align="right"
				/>
				<van-field
					v-model="records.submitmoneylimit"
					label="允许提交订单金额限制"
					type="number"
					placeholder="请输入活动底价"
					input-align="right"
				/>
				<van-cell title="活动开始时间">
					<div slot="right-icon" class="flex" @click="onToggleDatePicker('start')">
						<span>{{this.records.starttime_cn ? this.records.starttime_cn: '点击选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="活动结束时间">
					<div slot="right-icon" class="flex" @click="onToggleDatePicker('end')">
						<span>{{this.records.endtime_cn ? this.records.endtime_cn: '点击选择'}}</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">活动状态</div>
					<iswitch :value="records.status" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="records.status"></iswitch>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="砍价规则"></van-cell>
				<div class="activity-box" v-for="(item, index) in records.rules" :key="item.id">
					<div class="activity-col flex">
						<div>当前价格大于</div>
						<input type="text" v-model="item.range" class="input-rule">
						<div>砍价范围</div>
						<input type="text" v-model="item.range_start" class="input-rule">
						<div>到</div>
						<div class="input-unit" slot="right-icon">
							<input type="text" v-model="item.range_end">
							<span>元</span>
						</div>
						<div class="icon icon-close" @click="onDelRule(index)"></div>
					</div>
				</div>
			</van-cell-group>
			<div class="delete-rule"  @click="onAaddRule">添加砍价规则</div>
			<van-radio-group class="margin-10-t" v-model="records.usestatus" v-if="0">
				<van-cell-group>
					<van-cell title="消费方式"></van-cell>
					<van-cell title="到店消费" clickable @click="records.usestatus = '0'" class="border-0px">
						<van-radio name="0" />
					</van-cell>
					<van-cell title="快递上门" clickable @click="records.usestatus = '1'" class="border-0px">
						<van-radio name="1" />
					</van-cell>
					<van-cell title="同时支持快递与核销" clickable @click="records.usestatus = '2'" class="border-0px">
						<van-radio name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">高级设置</div>
					<iswitch :value="bestsetting" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="bestsetting"></iswitch>
				</van-cell>
			</van-cell-group>
			<template v-if="bestsetting == '1'">
				<van-cell-group class="margin-10-t">
					<van-field
						v-model="records.helplimit"
						label="好友帮砍限制数量"
						placeholder="填0或不填则无限制"
						input-align="right"
					/>
					<van-field
						v-model="records.dayhelplimit"
						label="每天帮砍好友人数限制"
						placeholder="填0或不填则无限制"
						input-align="right"
					/>
					<van-field
						v-model="records.joinlimit"
						label="参加人数限制"
						placeholder="填0或不填则无限制"
						input-align="right"
					/>
				</van-cell-group>
				<van-cell-group class="margin-10-t">
					<van-field
						v-model="records.falsejoinnum"
						label="虚拟参与人数"
						placeholder="请输入虚拟参与人数"
						input-align="right"
					/>
					<van-field
						v-model="records.falselooknum"
						label="虚拟浏览量"
						placeholder="请输入虚拟浏览量"
						input-align="right"
					/>
					<van-field
						v-model="records.falsesharenum"
						label="虚拟分享次数"
						placeholder="请输入虚拟分享次数"
						input-align="right"
					/>
				</van-cell-group>
			</template>
			<div class="padding-15">
				<van-button size="normal" block class="bg-info" @click="onSubmit">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--商品分类picker-->
		<van-popup v-model="goodsCategory" position="bottom">
			<van-picker
				show-toolbar
				title="所属分类"
				:columns="category"
				value-key="title"
				@cancel="onTogglePopup"
				@confirm="onConfirmCategory"
			/>
		</van-popup>
		<!--时间picker-->
		<van-popup class="popup-time" v-model="datePicker.status" position="bottom">
			<van-datetime-picker
				v-model="datePicker.currentDate"
				type="datetime"
				:min-data="datePicker.currentDate"
				:max-date="datePicker.maxDate"
				@confirm="onConfirmTime"
				@cancel="onToggleDatePicker"
			/>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	import Uploader from '@/components/uploader'
	export default {
		data() {
			return {
				id: 0,
				thumb: {},
				thumbs: [],
				goodsCategory: false,
				category: [],
				datePicker: {
					type: '',
					maxDate: new Date(2050, 12),
					currentDate: new Date(),
					status: false,
				},
				bestsetting: '0',
				showPreLoading: true,
				records: {
					name: '',
					cateid: '',
					category_title: '',
					status: '2',
					starttime_cn: '',
					endtime_cn: '',
					code: '',
					detail: '',
					rules: [],
					thumb: [],
					thumbs: [],
					helplimit: '0',
					dayhelplimit: '0',
					joinlimit: '0',
					falsejoinnum: '0',
					falselooknum: '0',
					falsesharenum: '0',
					//usestatus: 0,
				},
			}
		},
		components: {
			publicHeader,
			iswitch,
			Uploader
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'manage/kanjia/goods/post',
					data: {
						id: that.id
					},
					success: (res) => {
						that.records = that.util.extend(that.records, res.records);
						if(res.category){
							that.category = [...that.category, ...res.category];
						}
						if(that.records.thumbs && that.records.thumbs.length > 0) {
							for(var x in that.records.thumbs) {
								that.thumbs.push({
									url: that.records.thumbs[x]
								})
							}
						}
					}
				});
			},
			onChangeThumbs(data) {
				let thumbs = [];
				if(data && data.length > 0) {
					for(var x in data) {
						thumbs.push(data[x]['url'])
					}
				}
				this.records.thumbs = thumbs;
			},
			onTogglePopup(){
				this.goodsCategory = !this.goodsCategory;
			},
			onToggleDatePicker(type) {
				this.datePicker.status = !this.datePicker.status;
				if(type){
					this.datePicker.type = type;
				}
			},
			onConfirmTime(value){
				var time = this.util.formatDate(value);
				if(this.datePicker.type == 'start'){
					this.records.starttime_cn = time;
				} else {
					this.records.endtime_cn = time;
				}
				this.onToggleDatePicker();
			},
			onConfirmCategory(data){
				this.records.cateid = data.id;
				this.records.category_title = data.title;
				this.onTogglePopup();
			},
			onAaddRule(){
				var	rules = {
					range: '0',
					range_start: 0,
					range_end: 0,
				};
				this.records.rules.push(rules);
			},
			onDelRule(index){
				this.records.rules.splice(index, 1);
			},
			jsToggleSwitch(data){
				this.util.jsToggleSwitch({
					vue: this,
					key: data.keys,
					value: data.value
				});
			},
			onUploadImage(e) {
				var that = this;
				that.util.image({
					obj: e,
					success: function(obj, result) {
						if(result.url && result.filename) {
							that.thumb = {
								url: result.url,
								filename: result.filename,
							};
							that.thumb = Object.assign({}, that.thumb);
							that.records.thumb = that.thumb.url;
						}
					},
					options: {channel: 'h5'}
				});
			},
			onSubmit(){
				var that = this;
				if (!that.records.name) {
					that.util.$toast('名称不能为空', '', 1000);
					return false;
				}
				if (!that.records.oldprice) {
					that.util.$toast('商品原价不能为空', '', 1000);
					return false;
				}
				if (!that.records.price) {
					that.util.$toast('商品现价不能为空', '', 1000);
					return false;
				}
				setData({
					vue: that,
					url: 'manage/kanjia/goods/post',
					data: {
						id: that.id,
						data: that.records
					},
					redirect: that.util.getUrl({path: '/pages/gohome/kanjia/goods/list'}),
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#kanjiagoods-post .goods-thumb{
		width: 60px;
		height: 60px;
		border-radius: 3px;
	}
	#kanjiagoods-post .goods-thumb-wrap{
		position: relative;
	}
	#kanjiagoods-post .weui-uploader__input{
		display: block;
		width: 60px;
		height: 60px;
		opacity: 0;
		position: absolute;
		top: 0px;
		left: 3px;
	}
	#kanjiagoods-post .content .upload-img .uploader{
		padding: 0;
	}
	#kanjiagoods-post .content .upload-img .goods-img{
		padding-bottom: 10px;
		color: #333;
		font-size: 14px;
	}
	#kanjiagoods-post .content .activity-col{
		padding: 10px 15px;
		font-size: 12px;
	}
	#kanjiagoods-post .content .activity-col .input-rule{
		border: 1px solid #eee;
		width: 40px;
		height: 22px;
		text-align: center;
		color: #7a7a7a;
		border-radius: 0px;
		margin: 0 5px;
	}
	#kanjiagoods-post .content .activity-col .input-unit{
		margin-left: 5px;
	}
	#kanjiagoods-post .content .activity-col  .icon-close{
		font-size: 16px;
		padding-left: 10px;
	}
	#kanjiagoods-post .content .activity-col:nth-child(even){
		background-color: #F9F9F9;
	}
	#kanjiagoods-post .content .delete-rule{
		height: 44px;
		line-height: 44px;
		color: #33aafc;
		font-size: 14px;
		text-align: center;
		background-color: #fff;
	}
</style>


